import React, { useState, useEffect } from 'react';
import "./styles.css";
import { MDBDataTable } from 'mdbreact';
import { woHeader } from "./woHeader";

const DatatablePage = () => {

  const [users, setUsers] = useState([])

  const fetchDevs = async () => {
    try {
      const response = await fetch('http://restb:8080/restsql/res/devices?_output=json', {
        method: 'GET',
      });
      if (!response.ok) {
        throw new Error('EROL  Network response was not ok UPDATED ONE' + response.status);
      }
      const data = await response.json();
      const userDatae = {
        columns: woHeader,
        rows: data,
      };
      setUsers(userDatae);
    } catch (error) {
      console.error('EROL Error:', error);
    }
  }
  useEffect(() => {
    fetchDevs();
  }, []);

  return (
    <MDBDataTable
      striped
      bordered
      small
      data={users}
    />
  );
}

export default DatatablePage;